import request from '../request'


// 获取用户列表
export function listRole(data) {
    return request({
        url: '/api/role/listRole',
        method: 'post',
        data
    })
}

// 修改角色菜单和权限
export function updateRoleResource(data) {
    return request({
        url: '/api/role/updateRoleResource',
        method: 'post',
        data
    })
}

// 新增角色
export function saveRole(data) {
    return request({
        url: '/api/role/saveRole',
        method: 'post',
        data
    })
}

// 修改角色
export function updateRole(data) {
    return request({
        url: '/api/role/updateRole',
        method: 'post',
        data
    })
}

// 删除
export function deleteRoleById(params) {
    return request({
        url: '/api/role/deleteRoleById',
        method: 'get',
        params: params
    });
}
