import request from '../request'


// 获取所有菜单列表
export function listResourceInfo(data) {
    return request({
        url: '/api/resourceInfo/listResourceInfo',
        method: 'post',
        data
    })
}

// 获取所有菜单以及该角色的权限id集
export function listResourceInfoOrRole(roleId) {
    return request({
        url: '/api/resourceInfo/listResourceInfoOrRole?roleId=' + roleId,
        method: 'get'
    })
}

// 新增
export function saveResourceInfo(data) {
    return request({
        url: '/api/resourceInfo/saveResourceInfo',
        method: 'post',
        data
    })
}

// 修改
export function updateResourceInfo(data) {
    return request({
        url: '/api/resourceInfo/updateResourceInfo',
        method: 'post',
        data
    })
}

// 删除
export function deleteResourceInfoById(params) {
    return request({
        url: '/api/resourceInfo/deleteResourceInfoById',
        method: 'get',
        params: params
    });
}
