<template>
  <div id="styleVeiw">
    <el-drawer
        v-model="drawer"
        :title="$t('common.dialog.permissionTitle')"
        :direction="direction"
        :before-close="handleClose">
      <div class="demo-drawer__content">
        <div class="treeBox">
          <el-tree
              ref="tree"
              :data="data"
              show-checkbox
              node-key="id"
              :default-expand-all="true"
              :default-checked-keys="checkedKeys"
              :props="defaultProps"
              :highlight-current="true"
          />
        </div>
        <div class="drawerButton">
          <el-button type="primary" @click="getSelectedNode">
            {{ $t('common.button.confirm') }}
          </el-button>
          <el-button type="info" @click="closeButton()">
            {{ $t('common.button.cancel') }}
          </el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import {listResourceInfoOrRole} from "@/http/basics/ResourceInfo";
import {ElNotification} from "element-plus";
import {updateRoleResource} from "@/http/basics/roleApi";

export default {
  data() {
    return {
      drawer: false,
      direction: 'rtl',

      rowData: {},

      defaultProps: {
        children: 'resourceInfoList',
        label: 'name',
      },
      data: [],
      checkedKeys: [],
    };
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  methods: {

    // 列表数据请求
    resourceList(row) {
      this.rowData = row;
      listResourceInfoOrRole(row.id).then(res => {
        if (res.data.status == 200) {
          this.data = res.data.data.resourceInfoList;
          this.checkedKeys = res.data.data.roleResourecIds;
        }
      }).catch((erro) => { });
    },

    getSelectedNode() {
      // 选中的节点
      const checked = this.$refs.tree.getCheckedNodes();
      // 半选的节点
      const partial = this.$refs.tree.getHalfCheckedNodes();
      const addData = [...checked, ...partial];
      updateRoleResource({
        id: this.rowData.id,
        resourceInfoVoList: addData
      }).then(res => {
        this.closeButton();
        if (res.data.status == 200) {
          ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
        }
      }).catch((erro) => { });
    },

    closeButton() {
      this.checkedKeys = [];
      this.drawer = false;
    },

    handleClose(done) {
      this.closeButton();
      done();
    }
  }
};
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/deep/ .el-drawer__header {
  margin-bottom: 0;
}
.treeBox {
  height: 83vh;
  border: #989696 1px solid;
  overflow: auto;

}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
.drawerButton {
  position: absolute;
  bottom: 10px;
  padding-left: 10px;
}
</style>
